@import url(https://fonts.googleapis.com/css2?family=Spectral&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
    font-family: "Spectral", serif;
    font-size: 14px;
    padding: 30px 10px;
    font-weight: 400;
}

h1 {
    padding: 5px 15px;
    border-radius: 5px;
    border: 1px solid #ccc;
    position: relative;
    background: #f7f5ed;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    font-family: "Spectral", serif;
}

input {
    width: 100%;
}

.frame {
    display: flex;
    align-items: stretch;
}

.frame2 {
    min-width: 300px;
}

hr {
    border-top: 1px dashed #888;
}

input,
label,
button {
    padding: 5px 15px;
    border-radius: 5px;
    border: 1px solid #ccc;
    position: relative;
    background: #f7f5ed;
    font-size: 14px;
    font-weight: 600;
    font-family: "Spectral", serif;
}

label {
    background: #fff;
    min-width: 100px;
}

button {
    padding: 5px 15px;
    border-radius: 5px;
    border: 1px solid #222;
    position: relative;
    background: #cccccc;
    width: 75px;
    font-size: 14px;
    font-weight: 600;
    font-family: "Spectral", serif;
    min-width: 100px;
}

button:hover {
    background: #f7f5ed;
}

